  <template>
  <div id="app">
    <div id="deemed" v-if="getDeemState"></div>
    <div class="loader" :class="getloadingState?'active':''">
      <div class="loader-item">
        <div class="loader-wheel"></div>
        <div class="loader-text"></div>
      </div>
    </div>
    <template v-if="getRequestAllFlag" >
      <router-view :key="$route.fullPath"></router-view>
    </template>
  </div>
</template>

<script>
import '@/assets/js/jquery-1.9.1.min.js';
import { mapGetters } from 'vuex'
import { eventBus } from '@/main'
export default {
  name: 'App',
  components: {},
  created() {
    var filter = "win16|win32|win64|mac|macintel";
    
    if (navigator.platform && true) {
      if (filter.indexOf(navigator.platform.toLowerCase()) < 0) {
         // mobile
         if(location.href.indexOf("shop")===-1){
          if (location.host !== 'm.jrbaksa.com') {
            $(location).attr('href', 'https://m.jrbaksa.com');
            
          }
         }
       } else {
         // pc 
         if(location.href.indexOf("shop")===-1){
          if (location.host !== 'user.jrbaksa.com') {
            $(location).attr('href', 'https://user.jrbaksa.com');
            return
          }
         }
      }
    }
    this.$nextTick(function () {
      setTimeout(() => {
        this.$store.dispatch('REQUEST_ALL_CODE_LIST')
      }, 1000);
      
    })
		window.addEventListener("ns-bridge-ready",this.makeBridege );
  },
  mounted() {
    if(this.$store.getters.isAuthenticated){
        this.$store.dispatch("MEMBER_DETAIL")
    }
  },
  computed: {
    ...mapGetters(['getloadingState', 'getDeemState','getRequestAllFlag']),
  },
  methods:{
    
		makeBridege(e){
     	e.preventDefault()
			var nsWebViewBridge = e.detail || e.nsWebViewBridge;
			
				nsWebViewBridge.on('transContactsList', function (args) {
          const parameter = {
            type:'transContactsList',
            value:args
          }
          eventBus.mobileBridge(parameter)
				});
		},
  }
}
</script>

<style>
@import './assets/css/common.css';
@import './assets/css/loading.css';
@import './assets/css/context.css';
@import './assets/css/bootstrap-material-datetimepicker.css';
@import './assets/css/shop.css';
@import 'https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500&display=swap';
</style>
