import store from "@/store";
const onlyAuth = (to, from, next) => {
	if (!store.getters.isAuthenticated) {
		// 아직 로그인이 안 되있는 유저니까 막아야 한다.
		next("/login");
	} else {
		next();
	}
};

export default [	
	{
		meta: {
			title: 'Obituary'
		},
		path: "/obituary",
		name: "Obituary",
		component: () => import("@/views/obituary/Obituary.vue")
	},
	{
		meta: {
			title: 'ObituaryList'
		},
		path: "/obituaryList",
		name: "ObituaryList",
		beforeEnter: onlyAuth,
		component: () => import("@/views/obituary/ObituaryList.vue")
	},
	{
		meta: {
			title: 'Obituary'
		},
		path: "/obituaryReg",
		name: "ObituaryReg",
		beforeEnter: onlyAuth,
		component: () => import("@/views/obituary/ObituaryReg.vue")
	},
	{
		meta: {
			title: 'ObituaryUpdate'
		},
		path: "/obituaryUpdate",
		name: "ObituaryUpdate",
		beforeEnter: onlyAuth,
		component: () => import("@/views/obituary/ObituaryUpdate.vue")
	},
	{
		meta: {
			title: 'ObituaryForMobile'
		},
		path: "/obituaryForMobile",
		name: "ObituaryForMobile",
		component: () => import("@/views/obituary/ObituaryListForMobile.vue")
	},
];