import store from "@/store";
const onlyAuth = (to, from, next) => {
	if (!store.getters.isAuthenticated) {
		// 아직 로그인이 안 되있는 유저니까 막아야 한다.
		next("/login");
	} else {
		next();
	}
};

export default [
	{
		meta: {
			title: 'Main'
		},
		path: "/",
		name: "",
		beforeEnter: onlyAuth,
		component: () => import("@/views/Main.vue")
	},
];